          <tr>
            <th>Moneda</th>
            <th>Volumen BTC(24h)</th>
            <th>Precio de Apertura</th>
            <th>Precio de Cierre</th>
            <th>Minimo Valor</th>
            <th>Maximo Valor</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="fiat in ordenFiat" :key="fiat.id">
            <th scope="row">
              <img
                v-if="fiat.currency === 'ves'"
                src="../assets/img/VES.png"
                class="mb-1 mr-1"
                style="width: 25px; border-radius:50%"
                alt
              />
              <img
                v-if="fiat.currency === 'usd'"
                src="../assets/img/USD.jpg"
                class="mb-1 mr-1"
                style="width: 25px; border-radius:50%"
                alt
              />
              <img
                v-if="fiat.currency === 'eur'"
                src="../assets/img/EUR.jpg"
                class="mb-1 mr-1"
                style="width: 25px; border-radius:50%"
                alt
              />
              <img
                v-if="fiat.currency === 'rub'"
                src="../assets/img/RUB.svg"
                class="mb-1 mr-1"
                style="width: 25px; border-radius:50%"
                alt
              />
              <img
                v-if="fiat.currency === 'cny'"
                src="../assets/img/CNY.svg"
                class="mb-1 mr-1"
                style="width: 25px; border-radius:50%"
                alt
              />
              <router-link
                :to="{ name: 'currencies', params: {currency : fiat.currency} }"
              >{{fiat.currency.toUpperCase()}}</router-link>
            </th>
            <td>{{fiat.volume.toFixed(2)}}</td>
            <td>{{ fiat.open .toFixed(2)}}</td>
            <td>{{fiat.close.toFixed(2)}}</td>
            <td>{{fiat.min.toFixed(2)}}</td>
            <td>{{fiat.max.toFixed(2)}}</td>
          </tr>
        </tbody>
      </table>
    </div>-->
    <b-table :data="fiats" :default-sort-direction="defaultSortDirection" default-sort="volume">
      <template slot-scope="props">
        <b-table-column label="Moneda" width="85" sortable>
          <router-link :to="{ name: 'currencies', params: {currency : props.row.currency} }">
            <img
              v-if="props.row.currency === 'ves'"
              src="../assets/img/VES.png"
              class="mb-1 mr-1"
              style="width: 25px; border-radius:50%"
              alt
            />
            <img
              v-if="props.row.currency === 'usd'"
              src="../assets/img/USD.jpg"
              class="mb-1 mr-1"
              style="width: 25px; border-radius:50%"
              alt
            />
            <img
              v-if="props.row.currency === 'eur'"
              src="../assets/img/EUR.jpg"
              class="mb-1 mr-1"
              style="width: 25px; border-radius:50%"
              alt
            />
            <img
              v-if="props.row.currency === 'rub'"
              src="../assets/img/RUB.svg"
              class="mb-1 mr-1"
              style="width: 25px; border-radius:50%"
              alt
            />
            <img
              v-if="props.row.currency === 'cny'"
              src="../assets/img/CNY.svg"
              class="mb-1 mr-1"
              style="width: 25px; border-radius:50%"
              alt
            />
            {{props.row.currency.toUpperCase()}}
          </router-link>
        </b-table-column>

        <b-table-column
          field="close"
          label="Precio de Cierre"
          width="150"
          sortable
          numeric
        >{{props.row.close.toFixed(2)}}</b-table-column>

        <b-table-column
          field="%"
          label="%"
          width="40"
          sortable
        >{{parseFloat((props.row.close/props.row.open)-1 *100).toFixed(2)}}%</b-table-column>

        <b-table-column
          field="open"
          label="Precio de Apertura"
          sortable
          numeric
        >{{props.row.open.toFixed(2)}}</b-table-column>
        <b-table-column
          field="min"
          label="Valor Mínimo"
          sortable
          numeric
        >{{props.row.min.toFixed(2)}}</b-table-column>
        <b-table-column
          field="max"
          label="Valox Maximo"
          sortable
          numeric
        >{{props.row.max.toFixed(2)}}</b-table-column>
        <b-table-column
          field="volume"
          label="Volumen BTC(24h)"
          sortable
          numeric
        >{{props.row.volume.toFixed(2)}}</b-table-column>
      </template>
    </b-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "TableFiat",
  data() {
    return {
      fiats: [],
      fiatsA: [],
      msg: "Tabla Indicadores",
      defaultSortDirection: "desc"
    };
  },
  async mounted() {
    try {
      let result = await axios({
        method: "POST",
        url: "http://localhost:4000/",
        data: {
          query: `{
                        currencyChandle{
                            id
                            currency
                            opening
                            closing
                            min
                            max
                            volume
                            date
                        }
                    }`
        }
      });
      var hoy = new Date();
      var da = hoy.getDate() - 1;
      var dd = hoy.getDate();
      var mm = hoy.getMonth() + 1;
      var yyyy = hoy.getFullYear();
      if (da < 10) {
        da = "0" + da;
      }
      if (dd < 10) {
        dd = "0" + dd;
      }
      if (mm < 10) {
        mm = "0" + mm;
      }
      var ayer = `${yyyy}-${mm}-${da}`;
      var act = `${yyyy}-${mm}-${dd}`;
      var fecha = act.toString();
      var fechaA = ayer.toString();      
      this.ves = result.data.data.currencyChandle;
      let i = 0;
      this.ves.forEach(element => {
        i++;
        if (fecha == element.date) {
          this.fiats.push({
            close: element.closing,
            currency: element.currency,
            id: element.id,
            open: element.opening,
            min: element.min,
            max: element.max,
            volume: element.volume,
            date: element.date
          });
        } else if (this.fiats === "" && fechaA == element.date) {
          this.fiats.push({
            close: element.closing,
            currency: element.currency,
            id: element.id,
            open: element.opening,
            min: element.min,
            max: element.max,
            volume: element.volume,
            date: element.date
          });
        }
        if (fechaA == element.date) {
          this.fiatsA.push({
            name: element.currency,
            closeA: element.closing
          });
        }
      });      
    } catch (error) {
      console.log(error);
    }
  }
};
</script>
<style scoped>
th > a {
  text-decoration: none;
  color: black;
}
.bg-table {
  background: #dfe5e8;
}
</style>



