<template>
  <div class="chart" ref="chartdiv">
  </div>
</template>

<script>
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import am4themes_spiritedaway from "@amcharts/amcharts4/themes/spiritedaway.js";
import axios from 'axios'

am4core.useTheme(am4themes_spiritedaway);
am4core.useTheme(am4themes_animated);

export default {
  name: 'ChartEur',
  data(){
    return{
      resultado:[],
      Chandles:[]
    }
  },
  async mounted() {

            try {
            let result = await axios({
                method: 'POST',
                url: 'http://localhost:4000/',
                data: {
                    query: `{
                        currencyChandle{
                            id
                            currency
                            opening
                            closing
                            min
                            max
                            volume
                            date
                        }
                    }`
                }
            });
            this.Chandles = result.data.data.currencyChandle;
            let eur = 'eur'
            let i = 0
            this.Chandles.forEach(element => {
              i++
              if (eur == element.currency) {
                      this.resultado.push({
                      open: element.opening,
                      close:element.closing,
                      high: element.max,
                      low: element.min,
                      volume: element.volume,
                      date: element.date,
                  })
              }
          });
          console.log(this.resultado)
        } catch(error) {
            console.log(error);
        }

   let chart = am4core.create(this.$refs.chartdiv, am4charts.XYChart);
chart.paddingRight = 20;

chart.dateFormatter.inputDateFormat = "yyyy-MM-dd";

let dateAxis = chart.xAxes.push(new am4charts.DateAxis());
dateAxis.renderer.grid.template.location = 0;

let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
valueAxis.tooltip.disabled = true;

let series = chart.series.push(new am4charts.CandlestickSeries());
series.dataFields.dateX = "date";
series.dataFields.valueY = "close";
series.dataFields.openValueY = "open";
series.dataFields.lowValueY = "low";
series.dataFields.highValueY = "high";
series.simplifiedProcessing = true;
series.tooltipText = "Open:EUR.{openValueY.value}\nMin:EUR.{lowValueY.value}\nMax:EUR.{highValueY.value}\nClose:EUR.{valueY.value}";

chart.cursor = new am4charts.XYCursor();

// a separate series for scrollbar
let lineSeries = chart.series.push(new am4charts.LineSeries());
lineSeries.dataFields.dateX = "date";
lineSeries.dataFields.valueY = "close";
// need to set on default state, as initially series is "show"
lineSeries.defaultState.properties.visible = false;

// hide from legend too (in case there is one)
lineSeries.hiddenInLegend = true;
lineSeries.fillOpacity = 0.5;
lineSeries.strokeOpacity = 0.5;

let scrollbarX = new am4charts.XYChartScrollbar();
scrollbarX.series.push(lineSeries);
chart.scrollbarX = scrollbarX;

chart.data = this.resultado;
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.chart {
  width: 100%;
  height: 60vh;
}
</style>


