<template>
  <div id="calculadora">
    <div class="jumbotron jumbotron-fluid jb-bg">
            <div class="row">
            <div class="col-md-1"></div>
            <div class="col-sm-2 alert alert-light text-dark" style="border-radius:0;border-bottom: 2px solid #007bff">

                <div class="row">
                  <div class="col-sm-5">
                    <img
                      src="../assets/img/USDVES.jpg"
                      alt
                      class="my-lg-2 mr-lg-2 mr-sm-0 my-sm-0"
                      style="border-radius:10%; width:50px"
                    />                    
                  </div>
                  <div class="col-sm-7">
                    <p class="h6 my-lg-3 mx-0">VES.</p>
                      
                  </div>
                </div>
              </div>
            <div class="col-sm-2 alert alert-light text-dark ml-lg-5" style="border-radius:0;border-bottom: 2px solid #007bff">
              <div class="row">
                <div class="col-sm-5">
                  <img
                    src="../assets/img/VES.png"
                    alt
                    class="my-lg-2 mr-lg-2 mr-sm-0 my-sm-0"
                    style="border-radius:10%; width:50px"
                  />
                </div>
                <div class="col-sm-7">
                  <p class="h6 my-lg-3 mx-0">VES.</p>     
                    <p>{{ this.precio_moneda_ves.closing }}</p>                    
                </div>
              </div>
            </div>
            <div class="col-sm-2 alert alert-light text-dark ml-lg-5" style="border-radius:0;border-bottom: 2px solid #007bff">
              <div class="row">
                <div class="col-sm-5">
                  <img
                    src="../assets/img/USD.jpg"
                    alt
                    class="my-lg-2 mr-lg-2 mr-sm-0 my-sm-0"
                    style="border-radius:10%; width:50px; height:32px"
                  />
                </div>
                <div class="col-sm-7">
                  <p class="h6 my-lg-3 mx-0">$.</p>
                  <p>{{ this.precio_moneda_usd.closing }}</p>
                </div>
              </div>
            </div>
            </div>
      <div class="container-fluid">
        <div class="row" style="max-height:55vh;">
          <div class="col-sm-12 col-md-8 col-lg-7">
            <div class="row mb-sm-2">
              <div class="col-md-2 hidden-sm"></div>
              <div class="col-sm-12 col-md-10 p-5 mt-5 bg-calc">
                <div class="row">
                  <div class="col-md-1 hidden-sm"></div>
                  <div class="col-dm-12 col-md-11">
                    <div class="row">
                      <div class="form-group col-md-5 col-sm-3">
                        <label style="left:-20px; text-shadow: 0px 0px 3px #000000">Tengo</label>
                        <div class="input-group">
                          <div class="input-group-prepend">
                            <select v-model="from" name id class="input-group-text form-control">
                              <option :v-value="rate" v-for="rate in fromCoin" :key="rate.currency">
                                {{ rate.currency.toUpperCase() }}
                              </option>
                            </select>
                          </div>
                          <input v-on:keyup.self="convertirMonedaFrom()" v-model="cantidadFrom" class="form-control"
                            placeholder="Cantidad" v-on:keypress="isNumber()" @click="limpiarMonedaFrom()"/>
                        </div>
                      </div>
                      <div class="col-md-1 text-center" style="left:-7.5px; width: 80px">
                        <label for class="sr-only">Invertir</label>
                        <button :disabled="invertirDeshabilitado" @click="invertirMoneda()" class="btn btn-light p-0 mt-2">
                          <img src="../assets/img/logo.svg" style="width: 80px" alt />
                        </button>
                      </div>
                      <div class="form-group col-md-5 col-sm-3">
                        <label style="left:-20px; text-shadow: 0px 0px 3px #000000">Quiero</label>
                        <div class="input-group">
                          <div class="input-group-prepend">
                            <select v-model="to" class="form-control input-group-text">
                              <option :v-value="rate" v-for="rate in toCoin" :key="rate.currency">
                                {{ rate.currency.toUpperCase() }} 
                              </option>
                            </select>
                          </div>
                          <input v-on:keyup.self="convertirMonedaTo()" v-on:keypress="isNumber()" placeholder="Cantidad"
                              v-model="cantidadTo" class="form-control" @click="limpiarMonedaTo()"/>
                        </div>
                      </div>                     
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        <div class="hidden-sm col-md-4 col-lg-5">
            <img class="img-header mt-2" src="../assets/img/mapa.svg" alt style="max-width: 100%;" />
        </div>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
import gql from "graphql-tag";

const currencyChandle = gql`
  query currencyChandle {
    currencyChandle {
      id
      currency
      opening
      closing
      min
      max
      volume
      date
    }
  }
`;

const priceUsd = gql`
  query priceUsd {
    priceUsd{
      id
      price
      date
    }
  }
`;

export default {
  name: "Calculadora",
  data() {
    return {
      msg: "Calculadora dinamica",
      cantidad: null,
      from: 'USD',
      to: 'VES',
      rates: [],
      ves: [],
      priceUsd: [],
      usdBtc: [],
      resultado: 0,
      resultadoTo: 0,
      resultadoFrom: 0,
      cantidadFrom: 0,
      cantidadTo: 0,
      currencyChandle: [],
      precio_moneda_ves: [],
      precio_moneda_usd: [],
      precio_ves_usd: [] 
    };
  },

  apollo: {
    currencyChandle: currencyChandle,
    priceUsd: priceUsd
  },

  created() {
    //Creacion de variables para obtener la fecha actual dia, mes y año
    var hoy = new Date();
    var dd = hoy.getDate();
    var mm = hoy.getMonth() + 1;
    var yyyy = hoy.getFullYear();

    console.log(this.currencyChandle);
    
    
    //Condicional para agregar un 0 al dia y al mes de la fecha actual
    if (dd < 10) {
      dd = "0" + dd;
    }
    if (mm < 10) {
      mm = "0" + mm;
    }

    console.log(this.currencyChandle);
    console.log(this.priceUsd);

    //Concatenacion de variables para formar la fecha actual
    var act = `${yyyy}-${mm}-${dd}`;
    var fecha_actual = act.toString();

    //Creacion de variables para obtener la fecha de ayer del dia, mes y año
    var hoy_ayer = new Date();
    var dia_ayer = hoy_ayer.getDate() - 1;
    var mes_ayer = hoy_ayer.getMonth() + 1;
    var yyyy_ayer = hoy_ayer.getFullYear();
    
    //Condicional para agregar un 0 al dia y al mes de la fecha de ayer
    if (dia_ayer < 10) {
      dia_ayer = "0" + dia_ayer;
    }
    if (mes_ayer < 10) {
      mes_ayer = "0" + mes_ayer;
    }

    //Concatenacion de variables para formar la fecha de ayer
    var ayer = `${yyyy_ayer}-${mes_ayer}-${dia_ayer}`;    
    var fecha_ayer = ayer.toString();
    
    //Recorriendo el array con forEach donde se hace una condicional, si las fechas coinciden se hace un push en from
    this.currencyChandle.forEach((element) => {
      if(fecha_actual == element.date){        
        this.rates.push({
          currency: element.currency,
          closing: element.closing,
          date: element.date
        })        
      }   
    });
    
    //Push al elemento rates para que tenga la moneda(currency) BTC
    this.rates.push({
        currency: "BTC"
    });    
    
    //Push al elemento priceUsd para que tenga el precio del dolar para los valores de la calculadora
    this.priceUsd.forEach(element => {
        this.usdBtc.push({
          id: element.id,
          price: element.price,
          epoch: element.date
      });
    });
    
    //Push al array precio_moneda_ves
    this.rates.forEach(element => {
      if ('ves' === element.currency) {
        this.precio_moneda_ves.push({
          closing: element.closing
        })
      }
    });
    //console.log('Precio de la moneda ves');
    

    //Push al array precio_moneda_usd
    this.precio_moneda_usd = this.usdBtc[this.usdBtc.length - 1].price;
    
  },

  computed: {
    fromCoin() {
      let fromCoin = this.rates.filter(rates => rates.currency.toUpperCase() !== this.to);
      return fromCoin;
    },
    toCoin() {
      let toCoin = this.rates.filter(rates => rates.currency.toUpperCase() !== this.from);
      return toCoin;
    },
    formatearMoneda() {
      return Object.values(this.rates);
    },
    calcularResultadoTo() {
      return this.resultadoTo;
    },
    calcularResultadoFrom() {
      return this.resultadoFrom;
    },
    deshabilitado() {
      return this.From === 0 || !this.cantidadFrom || this.to === this.from;
    },
    invertirDeshabilitado() {
      return this.to === this.from;
    }
  },
  methods: {
    invertirMoneda() {
      let a = this.from;
      let b = this.to;
      this.from = b;
      this.to = a;
    },

    //Función para limpiar el input de cantidadFrom
    limpiarMonedaFrom(){
      let cantidad_from = 0.00;
        if (cantidad_from == this.cantidadFrom) {
            this.cantidadFrom = '';
        }
    },

    //Funcion para limpir el input de cantidadTo
    limpiarMonedaTo(){
      let cantidad_to = 0.00;
        if (cantidad_to == this.cantidadTo) {
            this.cantidadTo = '';
        }
    },

    //Funcion para sacar la tasa de conversion desde el from al to
    convertirMonedaFrom() {
      let precio_cierre_a;
      let precio_cierre_b;
      let cambio;
      let tasa_de_conversion;
      let cantidad_final = 0;      
      
      // Tengo -> from
      // Quiero -> to
      
      //Condición que evalua sí el from es igual a BTC y sí to es igual a USD
      if (this.from == "BTC" && this.to == "USD") {                      
        let priceUsdBtc = this.usdBtc[this.usdBtc.length - 1].price;
        cantidad_final = parseFloat(this.cantidadFrom);
        this.resultadoTo = priceUsdBtc * cantidad_final;
        this.cantidadTo = this.resultadoTo.toFixed(2);
        
      //Condición que evalua si el to es igual a BTC y sí el from es igual a USD
      } else if (this.to == "BTC" && this.from == "USD") {                        
        let priceUsdBtc = this.usdBtc[this.usdBtc.length - 1].price;
        cantidad_final = parseFloat(this.cantidadFrom);
        this.resultadoTo = (cantidad_final * 1) / priceUsdBtc;
        this.cantidadTo = this.resultadoTo.toFixed(8);

      //Condición que evalua si el from es igual a BTC
      } else if (this.from == "BTC") {
      
        //Recorriendo el array rates donde se evalua el elmento del array que coincida con la moneda del to
        this.rates.forEach((element) => {
          if (this.to.toLowerCase() == element.currency) {
            precio_cierre_b = element.closing;
          }
        });
        
        cantidad_final = parseFloat(this.cantidadFrom);
        this.resultadoTo = precio_cierre_b * cantidad_final;
        this.cantidadTo = this.resultadoTo.toFixed(2);

      //Condición que evalua sí el to es igual a BTC
      } else if (this.to == "BTC") {
        
        //Recorriendo el array rates donde se evalua el elmento del array que coincida con la moneda del from
        this.rates.forEach((element) => {
          if (this.from.toLowerCase() == element.currency) {
            precio_cierre_a = element.closing;
          }
        });

        cantidad_final = parseFloat(this.cantidadFrom);
        this.resultadoTo = (cantidad_final * 1) / precio_cierre_a;
        this.cantidadTo = this.resultadoTo.toFixed(8);

      //Condición que evalua sí el from es igual a USD
      } else if (this.from == "USD") {

        //Recorriendo el array rates donde se evalua el elmento del array que coincida con la moneda del to
        this.rates.forEach((element) => {
          if (this.to.toLowerCase() == element.currency) {
            precio_cierre_b = element.closing;
          }
        });

        let priceUsdBtc = this.usdBtc[this.usdBtc.length - 1].price;
        precio_cierre_a = priceUsdBtc;        
        cambio = this.cantidadFrom;
        tasa_de_conversion = precio_cierre_b / precio_cierre_a;
        this.resultadoTo = tasa_de_conversion * this.cantidadFrom;
        this.cantidadTo = this.resultadoTo.toFixed(2); 
      
      //Condición que evalua sí el to es igual a USD
      } else if (this.to === "USD") {

        //Recorriendo el array rates donde se evalua el elmento del array que coincida con la moneda del from
        this.rates.forEach((element) => {
          if (this.from.toLowerCase() == element.currency) {
            precio_cierre_a = element.closing;
          }
        });

        let priceUsdBtc = this.usdBtc[this.usdBtc.length - 1].price;        
        precio_cierre_b = priceUsdBtc;
        cambio = this.cantidadFrom;
        tasa_de_conversion = precio_cierre_b / precio_cierre_a;
        this.resultadoTo = tasa_de_conversion * this.cantidadFrom;
        this.cantidadTo = this.resultadoTo.toFixed(2);

      //Condición que evalua sí el from es diferente a BTC y sí el to es diferente a BTC
      } else if (this.from != "BTC" && this.to != "BTC") {        
        
        //Recorriendo el array rates donde se evalua el elmento del array que coincida con la moneda del from
        this.rates.forEach((element) => {
          if(this.from.toLowerCase() == element.currency){
            precio_cierre_a = element.closing
          }
        });

        //Recorriendo el array rates donde se evalua el elmento del array que coincida con la moneda del to
        this.rates.forEach((element) => {
          if(this.to.toLowerCase() == element.currency){
            precio_cierre_b = element.closing
          }
        })        

        let priceUsdBtc = this.usdBtc[this.usdBtc.length - 1].price;  
        cambio = this.cantidadFrom;
        tasa_de_conversion = precio_cierre_b / precio_cierre_a;
        this.resultadoTo = tasa_de_conversion * this.cantidadFrom;
        this.cantidadTo = this.resultadoTo.toFixed(2);
      }
    },

    //Funcion para sacar la tasa de conversion del to al from
    convertirMonedaTo() {
      let precio_cierre_a;
      let precio_cierre_b;
      let cambio;
      let tasa_de_conversion;
      let cantidad_final_to;

      //Condición que evalua si el from es igual a USD y sí el to es igual a BTC
      if (this.from === "USD" && this.to === "BTC") {        

        let priceUsdBtc = this.usdBtc[this.usdBtc.length - 1].price;
        cantidad_final_to = parseFloat(this.cantidadTo);
        this.resultadoTo = priceUsdBtc * cantidad_final_to;
        this.cantidadFrom = this.resultadoTo.toFixed(2);

      //Condición que evalua si el to es igual a USD y sí el from es igual a BTC
      } else if (this.to === "USD" && this.from === "BTC") {
        
        let priceUsdBtc = this.usdBtc[this.usdBtc.length - 1].price;
        cantidad_final_to = parseFloat(this.cantidadTo);
        this.resultadoFrom = cantidad_final_to / priceUsdBtc;
        this.cantidadFrom = this.resultadoFrom.toFixed(2);
        
      //Condición que evalua si el to es igual a BTC
      } else if (this.to === "BTC") {        

        this.rates.forEach((element) => {
          if(this.from.toLowerCase() == element.currency){
            precio_cierre_b = element.closing
          }
        })  

        cantidad_final_to = parseFloat(this.cantidadTo);
        this.resultadoFrom = precio_cierre_b * cantidad_final_to;
        this.cantidadFrom = this.resultadoFrom.toFixed(2);

      //Condición que evalua si el from es igual a BTC
      } else if (this.from === "BTC") {    

        this.rates.forEach((element) => {
          if(this.to.toLowerCase() == element.currency){
            precio_cierre_a = element.closing
          }
        })  
        
        cantidad_final_to = parseFloat(this.cantidadTo);
        this.resultadoFrom = (cantidad_final_to * 1) / precio_cierre_a;
        this.cantidadFrom = this.resultadoFrom.toFixed(8);

      //Condición que evalua si el from es igual a USD
      } else if (this.from === "USD") {
        
        this.rates.forEach((element) => {
          if(this.to.toLowerCase() == element.currency){
            precio_cierre_b = element.closing
          }
        })  

        let priceUsdBtc = this.usdBtc[this.usdBtc.length - 1].price;
        precio_cierre_a = priceUsdBtc;        
        cambio = parseFloat(this.cantidadTo);
        tasa_de_conversion = precio_cierre_a / precio_cierre_b;
        this.resultadoFrom = tasa_de_conversion * cambio;
        this.cantidadFrom = this.resultadoFrom.toFixed(2);

      //Condición que evalua si el to es igual a USD
      } else if (this.to === "USD") {              

        this.rates.forEach((element) => {
          if(this.from.toLowerCase() == element.currency){
            precio_cierre_a = element.closing
          }
        });

        let priceUsdBtc = this.usdBtc[this.usdBtc.length - 1].price;    
        precio_cierre_b = priceUsdBtc;
        cambio = parseFloat(this.cantidadTo);
        tasa_de_conversion = precio_cierre_a / precio_cierre_b;
        this.resultadoFrom = tasa_de_conversion * cambio;
        this.cantidadFrom = this.resultadoFrom.toFixed(2);        

      //Condición que evalua si el to es diferente a BTC y sí el from es diferente a BTC
      } else if (this.to != "BTC" && this.from != "BTC") {
        
        this.rates.forEach((element) => {
          if(this.to.toLowerCase() == element.currency){
            precio_cierre_a = element.closing
          }
        })  

        this.rates.forEach((element) => {
          if(this.from.toLowerCase() == element.currency){
            precio_cierre_b = element.closing
          }
        })
        
        tasa_de_conversion = precio_cierre_b / precio_cierre_a;
        this.resultadoFrom = tasa_de_conversion * this.cantidadTo;
        this.cantidadFrom = this.resultadoFrom.toFixed(2);
      }
    },

    isNumber: function(evt) {
      evt = evt ? evt : window.event;
      let charCode = evt.which ? evt.which : evt.keyCode;
      if (
        charCode > 31 &&
        (charCode < 48 || charCode > 57) &&
        charCode !== 46
      ) {
        evt.preventDefault();
      } else {
        return true;
      }
    }
  },
  watch: {
    from() { 
      this.resultadoFrom = 0;
    },
    to() {
      this.resultadoTo = 0;
    },
    cantidadFrom(newValue, oldValue) {
      if (/^(\d{1,12}\.?(\d{0,8})?)$/.test(newValue) || newValue === "") {
        this.c = parseFloat(newValue);
      } else {
        this.cantidadFrom = oldValue;
      }
    },
    cantidadTo(newValue, oldValue) {         
      if (/^(\d{1,12}\.?(\d{0,8})?)$/.test(newValue) || newValue === "") {
        this.c = parseFloat(newValue);
      } else {
        this.cantidadTo = oldValue;
      }
    }
  }
};
</script>
<style scoped>
.input-group-prepend {
  background: #bdc0bf;
  color: black;
  border-radius: 5px 0 0 5px;
}
.input-group-prepend > select {
  color: black;
  border: 0px solid transparent;
}
.input-group-prepend > select > option {
  color: black;
}
.bg-calc {
  background: #0071bc;
  /* fallback for old browsers */

  border-radius: 5px;
  color: white;
}
.jumbotron {
  margin-bottom: 0 !important;
}

.input-group-text {
  background: transparent;
}
.jb-bg {
  background: url("../assets/img/img-header.svg") no-repeat;
  background-size: cover;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0,.4);
}
@media (max-width: 576px) {
  .jb-bg {
    background: white;
  }
}

input[type="radio"] {
  display: none;
}
input[type="radio"]:checked + .btn-outline-primary {
  background: #007bff !important;
}

label {
  padding-left: 1.3em;
  position: relative;
}
</style>



