<template>
  <div class="currencies">
    <div class="container-fluid">
      <div class="row mt-5">
        <!--<h1>{{ $route.params.currency}}</h1>-->
        <div class="col-sm-12 col-md-4 col-lg-2 mt-5">
          <div class="mb-3" style="max-width: 18rem;">
            <div class="card-body text-secondary">
              <div
                class="text-center"
                v-if="$route.params.currency === 'ves' || $route.params.currency === 'VES'"
              >
                <img
                  src="../assets/img/VES.png"
                  style="border-radius:10%"
                  width="150px"
                  height="100px"
                  alt
                />
                <hr />
                <h5 class="card-title">Descripcion</h5>
                <div class="text-left">
                  <div v-for="coin in ves" :key="coin.id">
                    <p class="card-text">Moneda: {{coin.name.toUpperCase()}}</p>
                    <p class="card-text">Precio Minimo: {{coin.min.toFixed(2)}}</p>
                    <p class="card-text">Precio Maximo: {{coin.max.toFixed(2)}}</p>
                    <p class="card-text">Volumen BTC(24h): {{coin.volume.toFixed(2)}}</p>
                    <p class="card-text">Precio de Apertura: {{coin.open.toFixed(2)}}</p>
                    <p class="card-text">Precio de Cierre: {{coin.close.toFixed(2)}}</p>
                  </div>
                </div>
              </div>
              <div
                class="text-center"
                v-else-if="$route.params.currency === 'usd' || $route.params.currency === 'USD'"
              >
                <img
                  src="../assets/img/USD.jpg"
                  style="border-radius:10%"
                  width="150px"
                  height="100px"
                  alt
                />
                <hr />
                <h5 class="card-title">Descripcion</h5>
                <div class="text-left">
                  <div v-for="coin in usd" :key="coin.id">
                    <p class="card-text">Moneda: {{coin.name.toUpperCase()}}</p>
                    <p class="card-text">Precio Minimo: {{coin.min.toFixed(2)}}</p>
                    <p class="card-text">Precio Maximo: {{coin.max.toFixed(2)}}</p>
                    <p class="card-text">Volumen BTC(24h): {{coin.volume.toFixed(2)}}</p>
                    <p class="card-text">Precio de Apertura: {{coin.open.toFixed(2)}}</p>
                    <p class="card-text">Precio de Apertura: {{coin.close.toFixed(2)}}</p>
                  </div>
                </div>
              </div>
              <div
                class="text-center"
                v-else-if="$route.params.currency === 'eur' || $route.params.currency === 'EUR'"
              >
                <img
                  src="../assets/img/EUR.jpg"
                  style="border-radius:10%"
                  width="150px"
                  height="100px"
                  alt
                />
                <hr />
                <h5 class="card-title">Descripcion</h5>
                <div class="text-left">
                  <div v-for="coin in eur" :key="coin.id">
                    <p class="card-text">Moneda: {{coin.name.toUpperCase()}}</p>
                    <p class="card-text">Precio Minimo: {{coin.min.toFixed(2)}}</p>
                    <p class="card-text">Precio Maximo: {{coin.max.toFixed(2)}}</p>
                    <p class="card-text">Volumen BTC(24h): {{coin.volume.toFixed(2)}}</p>
                    <p class="card-text">Precio de Apertura: {{coin.open.toFixed(2)}}</p>
                    <p class="card-text">Precio de Apertura: {{coin.close.toFixed(2)}}</p>
                  </div>
                </div>
              </div>
              <div
                class="text-center"
                v-else-if="$route.params.currency === 'rub' || $route.params.currency === 'RUB'"
              >
                <img
                  src="../assets/img/RUB.svg"
                  style="border-radius:10%;  border:1px solid #c7c7c7"
                  width="150px"
                  height="100px"
                  alt
                />
                <hr />
                <h5 class="card-title">Descripcion</h5>
                <div class="text-left">
                  <div v-for="coin in rub" :key="coin.id">
                    <p class="card-text">Moneda: {{coin.name.toUpperCase()}}</p>
                    <p class="card-text">Precio Minimo: {{coin.min.toFixed(2)}}</p>
                    <p class="card-text">Precio Maximo: {{coin.max.toFixed(2)}}</p>
                    <p class="card-text">Volumen BTC(24h): {{coin.volume.toFixed(2)}}</p>
                    <p class="card-text">Precio de Apertura: {{coin.open.toFixed(2)}}</p>
                    <p class="card-text">Precio de Apertura: {{coin.close.toFixed(2)}}</p>
                  </div>
                </div>
              </div>
              <div
                class="text-center"
                v-else-if="$route.params.currency === 'cny' || $route.params.currency === 'CNY'"
              >
                <img
                  src="../assets/img/CNY.svg"
                  style="border-radius:10%;"
                  width="150px"
                  height="100px"
                  alt
                />
                <hr />
                <h5 class="card-title">Descripcion</h5>
                <div class="text-left">
                  <div v-for="coin in cny" :key="coin.id">
                    <p class="card-text">Moneda: {{coin.name.toUpperCase()}}</p>
                    <p class="card-text">Precio Minimo: {{coin.min.toFixed(2)}}</p>
                    <p class="card-text">Precio Maximo: {{coin.max.toFixed(2)}}</p>
                    <p class="card-text">Volumen BTC(24h): {{coin.volume.toFixed(2)}}</p>
                    <p class="card-text">Precio de Apertura: {{coin.open.toFixed(2)}}</p>
                    <p class="card-text">Precio de Apertura: {{coin.close.toFixed(2)}}</p>
                  </div>
                </div>
              </div>
              <div v-else></div>
            </div>
          </div>
        </div>
        <div
          class="col-sm-12 col-md-8 col-lg-10 mt-5"
          v-if="$route.params.currency === 'ves' || $route.params.currency === 'VES'"
        >
          <h3 class="text-center display-5">VES/BTC</h3>
          <hr class="mt-0" />
          <Chart class="mt-5" />
        </div>
        <div
          class="col-sm-12 col-md-8 col-lg-10 mt-5"
          v-if="$route.params.currency === 'usd' || $route.params.currency === 'USD'"
        >
          <h3 class="text-center display-5">USD/BTC</h3>
          <hr class="mt-0" />
          <ChartUsd class="mt-5" />
        </div>
        <div
          class="col-sm-12 col-md-8 col-lg-10 mt-5"
          v-if="$route.params.currency === 'eur' || $route.params.currency === 'EUR'"
        >
          <h3 class="text-center display-5">EUR/BTC</h3>
          <hr class="mt-0" />
          <ChartEur class="mt-5" />
        </div>
        <div
          class="col-sm-12 col-md-8 col-lg-10 mt-5"
          v-if="$route.params.currency === 'rub' || $route.params.currency === 'RUB'"
        >
          <h3 class="text-center display-5">RUB/BTC</h3>
          <hr class="mt-0" />
          <ChartRub class="mt-5" />
        </div>
        <div
          class="col-sm-12 col-md-8 col-lg-10 mt-5"
          v-if="$route.params.currency === 'cny' || $route.params.currency === 'CNY'"
        >
          <h3 class="text-center display-5">CNY/BTC</h3>
          <hr class="mt-0" />
          <ChartCny class="mt-5" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Chart from "./Chart";
import ChartUsd from "./ChartUsd";
import ChartEur from "./ChartEur";
import ChartRub from "./ChartRub";
import ChartCny from "./ChartCny";
export default {
  name: "Currencies",
  data() {
    return {
      datosApi: [],
      usd: [],
      ves: [],
      eur: [],
      rub: [],
      cny: []
    };
  },
  async mounted() {
    try {
      let result = await axios({
        method: "POST",
        url: "http://localhost:4000/",
        data: {
          query: `{
                        currencyChandle{
                            id
                            currency
                            opening
                            closing
                            min
                            max
                            volume
                            date
                        }
                    }`
        }
      });
      var hoy = new Date();
      var da = hoy.getDate() - 1;
      var dd = hoy.getDate();
      var mm = hoy.getMonth() + 1;
      var yyyy = hoy.getFullYear();
      if (da < 10) {
        da = "0" + da;
      }
      if (dd < 10) {
        dd = "0" + dd;
      }
      if (mm < 10) {
        mm = "0" + mm;
      }
      var ayer = `${yyyy}-${mm}-${da}`;
      var act = `${yyyy}-${mm}-${dd}`;
      var fecha = act.toString();
      var fechaA = ayer.toString();
      this.datosApi = result.data.data.currencyChandle;
      let usd = "usd";
      let ves = "ves";
      let eur = "eur";
      let rub = "rub";
      let cny = "cny";
      let i = 0;
      this.datosApi.forEach(element => {
        i++;
        if (usd == element.currency && fecha == element.date) {
          this.usd.push({
            name: element.currency,
            open: element.opening,
            close: element.closing,
            max: element.max,
            min: element.min,
            volume: element.volume,
            date: element.date
          });
        } else if (
          this.usd === "" &&
          usd == element.currency &&
          fechaA == element.date
        ) {
          this.usd.push({
            name: element.currency,
            open: element.opening,
            close: element.closing,
            max: element.max,
            min: element.min,
            volume: element.volume,
            date: element.date
          });
        }
        if (ves == element.currency && fecha == element.date) {
          this.ves.push({
            name: element.currency,
            open: element.opening,
            close: element.closing,
            max: element.max,
            min: element.min,
            volume: element.volume,
            date: element.date
          });
        } else if (
          this.ves === "" &&
          ves == element.currency &&
          fechaA == element.date
        ) {
          this.ves.push({
            name: element.currency,
            open: element.opening,
            close: element.closing,
            max: element.max,
            min: element.min,
            volume: element.volume,
            date: element.date
          });
        }
        if (eur == element.currency && fecha == element.date) {
          this.eur.push({
            name: element.currency,
            open: element.opening,
            close: element.closing,
            max: element.max,
            min: element.min,
            volume: element.volume,
            date: element.date
          });
        } else if (
          this.eur === "" &&
          eur == element.currency &&
          fechaA == element.date
        ) {
          this.eur.push({
            name: element.currency,
            open: element.opening,
            close: element.closing,
            max: element.max,
            min: element.min,
            volume: element.volume,
            date: element.date
          });
        }
        if (rub == element.currency && fecha == element.date) {
          this.rub.push({
            name: element.currency,
            open: element.opening,
            close: element.closing,
            max: element.max,
            min: element.min,
            volume: element.volume,
            date: element.date
          });
        } else if (
          this.rub === "" &&
          rub == element.currency &&
          fechaA == element.date
        ) {
          this.rub.push({
            name: element.currency,
            open: element.opening,
            close: element.closing,
            max: element.max,
            min: element.min,
            volume: element.volume,
            date: element.date
          });
        }
        if (cny == element.currency && fecha == element.date) {
          this.cny.push({
            name: element.currency,
            open: element.opening,
            close: element.closing,
            max: element.max,
            min: element.min,
            volume: element.volume,
            date: element.date
          });
        } else if (
          this.cny === "" &&
          cny == element.currency &&
          fechaA == element.date
        ) {
          this.cny.push({
            name: element.currency,
            open: element.opening,
            close: element.closing,
            max: element.max,
            min: element.min,
            volume: element.volume,
            date: element.date
          });
        }
      });
    } catch (error) {
      console.log(error);
    }
  },
  components: {
    Chart,
    ChartUsd,
    ChartEur,
    ChartRub,
    ChartCny
  }
};
</script>


